import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { FormsModule } from '@angular/forms';

import { UserService } from '../../../../api/services/user.service';
import { LookupService } from '../../../../api/services/lookup.service';
import { SharedService } from '../../../../shared/services/shared.service';

import { ApartmentService } from '../../../../api/services/apartment.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-profile-basic',
  templateUrl: './profile-basic.component.html',
  styleUrls: ['./profile-basic.component.scss']
})
export class ProfileBasicComponent implements OnInit {
 
  user:any;
  bloodGroupData:any;
  genderType:string = "";

  isUserDataLoaded:boolean = false;

  isEdit:boolean = false;

  constructor(
  	private userService:UserService,
    private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { }

  getGender(id){
  	return id == '43' ? 'Male' : 'Female'
  }

  getDOB(date){
  	if(date === undefined || date.length == 0) {
  		return ""
  	}
  	else {
  		return moment(date).format("DD-MM-YY");
  	}
    
  }

  showEditForm(){
  	this.isEdit = !this.isEdit;
  }

  submitProfileBasicForm(form) {

  	this.isUserDataLoaded = false;

  	let userDetails = {
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "firstName": this.user.firstName,
        "middleName": "string",
        "lastName": this.user.lastName,
        "emailId": this.user.emailId,
        "password": this.user.password,
        "salt": null,
        "address1": this.user.address1 || "",
        "address2": "string",
        "city": "string",
        "state": "string",
        "country": "string",
        "zipCode": "string",
        "genderId": parseInt(this.genderType),
        "dob": this.user.dob,
        "joinedOn": "2019-11-10T09:58:08.934Z",
        "bloodGroup": this.user.bloodGroup,
        "phoneNumber": this.user.phoneNumber.toString(),
        "emergencyContactNumber": "string",
        "emergencyContactPerson": "string",
        "emergencyContactNumberSecondary": "string",
        "emergencyContactPersonSecondary": "string",
        "accessCardNumber": "string",
        "notes": "string",
        "profilePictureId": null,
        "isLiving": true,
        "isActive": true,
        "isApproved": true,
        "resetPassword": true,
        "insertedBy": 0,
        "updatedBy": parseInt(this.cookieService.get('userId'))
      }

      this.userService.UpdateUser(userDetails).subscribe((res:any) => {
            
            if(res.message) {
            	this.isUserDataLoaded = true;
            	this.isEdit = false;
            	this.sharedService.setUserDetails(userDetails);
            	this.sharedService.setAlertMessage("User updated successfully");
            }

	  },
	  error => {

	  });

  }

  ngOnInit() {

  	this.user = {};
  	this.user.bloodGroup = "";
  	

  	this.userService.getUserById(parseInt(this.cookieService.get('userId'))).subscribe((res:any) => {

  		this.user = res[0];
  		console.log(this.user);
  		this.genderType = '' + this.user.genderId;

  		this.isUserDataLoaded = true;

  	},
    error => {
      
    });

    this.lookupService.getLookupValueByLookupTypeId(24).subscribe((res:any) => {
      this.bloodGroupData = res;
    });

  }

}
