import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from '../.././auth.guard';

import { MyProfileComponent } from './my-profile.component';
import { VehiclesComponent } from './components/vehicles/vehicles.component';
import { FamilyMembersComponent } from './components/family-members/family-members.component';
import { PetComponent } from './components/pet/pet.component';
import { PropertyManagerComponent } from './components/property-manager/property-manager.component';
import { EmergencyContactComponent } from './components/emergency-contact/emergency-contact.component';
import { MyStaffsComponent } from './components/my-staffs/my-staffs.component';
import { ProfileUnitDetailsComponent } from './components/profile-unit-details/profile-unit-details.component';
import { ProfileUnitStaffsComponent } from './components/profile-unit-staffs/profile-unit-staffs.component';
import { ProfileLeaseInfoComponent } from './components/profile-lease-info/profile-lease-info.component';
import { ProfileInterestGroupComponent } from './components/profile-interest-group/profile-interest-group.component';
import { ProfileBasicComponent } from './components/profile-basic/profile-basic.component';
import { ProfileUserAbsenceComponent } from './components/profile-user-absence/profile-user-absence.component';

const routes: Routes = [
	
	{ path: '', component: MyProfileComponent,
	
		children: [
			{ path: '', redirectTo: 'basic', pathMatch: 'full'},
			{ path: 'basic', component: ProfileBasicComponent},
			{ path: 'unit', component: ProfileUnitDetailsComponent},
			{ path: 'staffs', component: ProfileUnitStaffsComponent},
			{ path: 'vehicles', component: VehiclesComponent },
			{ path: 'pets', component: PetComponent },
			{ path: 'propertymanager', component: PropertyManagerComponent},
			{ path: 'family', component: FamilyMembersComponent},
			{ path: 'lease', component: ProfileLeaseInfoComponent},
			{ path: 'interest', component: ProfileInterestGroupComponent},
			{ path: 'absence', component: ProfileUserAbsenceComponent},
			{ path: '**', redirectTo: 'basic', pathMatch: 'full'}
		]

	},
	{ path: 'myprofile/basic', component: ProfileBasicComponent},
	{ path: 'myprofile/unit', component: ProfileUnitDetailsComponent},
	{ path: 'myprofile/staffs', component: ProfileUnitStaffsComponent},
	{ path: 'myprofile/vehicles', component: VehiclesComponent },
	{ path: 'myprofile/pets', component: PetComponent },
	{ path: 'myprofile/propertymanager', component: PropertyManagerComponent},
	{ path: 'myprofile/family', component: FamilyMembersComponent},
	{ path: 'myprofile/lease', component: ProfileLeaseInfoComponent},
	{ path: 'myprofile/interest', component: ProfileInterestGroupComponent},
	{ path: 'myprofile/absence', component: ProfileUserAbsenceComponent},

	
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MyProfileRoutingModule { }
